@import url("https://emoji-css.afeld.me/emoji.css");

@font-face {
  font-family: "Pattaya";
  src: url(assets/fonts/Pattaya-Regular.ttf);
}

body {
  font-family: "Pattaya";
}

hr {
  border: 0;
  height: 1px;
  background-image: linear-gradient(
    to right,
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0.75),
    rgba(0, 0, 0, 0)
  );
}

.container-box {
  width: 80%;
  background-color: transparent;
  position: relative;
  z-index: 9;
}

.particles-box {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}
